body {
  font-family: "Inter", sans-serif;
  font-size: 18px;
  line-height: 21px;
  position: relative;
  overflow-x: hidden !important;
}

img {
  max-width: 100%;
  height: auto;
}

a {
  font-family: inherit;
  display: inline-block;
  text-decoration: none;
  color: inherit;
}

b,
strong {
  font-weight: 500;
}

address {
  color: inherit;
}

button {
  cursor: pointer;
}

ul,
ol {
  margin: 0;
  padding: 0;
}

.container {
  max-width: 1920px;
  margin: 0 auto;
  padding-left: 150px;
  padding-right: 150px;
  box-sizing: border-box;
}

.vh {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.title {
  color: #121212;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
}

.title--large {
  font-size: 100px;
  line-height: 100px;
}

.title--big {
  font-size: 70px;
  line-height: 70px;
}

.title--medium {
  font-size: 50px;
  line-height: 50px;
}

.title--small {
  font-size: 36px;
  line-height: 36px;
}

.title--tiny {
  font-size: 32px;
  line-height: 32px;
}

.text {
  font-size: 18px;
  line-height: 21px;
}

.text ul {
  padding-left: 20px;
}

.text ul li {
  list-style: disc;
  margin-bottom: 8px;
}

.text ol {
  padding-left: 20px;
}

.link {
  font-family: "Inter", sans-serif;
  font-size: 15px;
  line-height: 18px;
  font-weight: 700;
  text-transform: uppercase;
  color: #121212;
  position: relative;
}

.link--social {
  text-decoration: underline;
}

.link--arrow {
  display: inline-flex;
  align-items: center;
}

.link--arrow::after {
  content: '';
  display: inline-flex;
  width: 20px;
  height: 20px;
  background: url(../img/icon-arrow-right.svg) no-repeat center / contain;
  transform: translateX(15px);
  transition: transform 0.3s ease;
}

.link--arrow:hover::after {
  transform: translateX(25px);
}

.link--arrowBack {
  display: inline-flex;
  align-items: center;
  padding-left: 14px;
}

.link--arrowBack::before {
  content: '';
  display: inline-flex;
  width: 20px;
  height: 20px;
  background: url(../img/icon-arrow-left.svg) no-repeat center / contain;
  transform: translateX(-15px);
  transition: transform 0.3s ease;
}

.link--arrowBack:hover::before {
  transform: translateX(-25px);
}

.caption {
  font-size: 13px;
  line-height: 16px;
  font-weight: 700;
  color: #A2A9B4;
  text-transform: uppercase;
}

button {
  background: none;
  border: none;
  padding: 0;
}

.button {
  padding: 16px 80px;
  background: #fff;
  font-family: "Bebas Neue", sans-serif;
  font-weight: 700;
  font-size: 20px;
  border: 2px solid transparent;
  border-radius: 200px;
  text-transform: uppercase;
}

.button--green {
  background-color: #E5F860;
  border-color: #121212;
}

.button--black {
  background-color: #121212;
  color: #E5F860;
}

.controls {
  display: flex;
  justify-content: flex-end;
}

.controls__button {
  margin: 0 12px;
}

.controls__button img {
  width: 26px;
  height: 26px;
}

.section__head {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
}

.section__title {
  transform: translateY(-40%);
}

.section__link {
  max-width: 190px;
}

figure {
  display: block;
  max-width: 780px;
  margin: 70px auto 44px;
}

figure img {
  width: 100%;
}

figure figcaption {
  margin-top: 10px;
  font-size: 13px;
  line-height: 15px;
  text-align: left;
  color: #7B8592;
}

blockquote {
  margin-top: 50px;
  padding: 24px 30px;
  background-color: #F8F8FB;
  border-left: 3px solid #121212;
  font-size: 36px;
  line-height: 36px;
  font-family: "Bebas Neue", serif;
}

blockquote a {
  text-decoration: underline;
}

.roundBlock {
  display: inline-block;
  padding: 0 30px;
  height: 55px;
  font-family: "Bebas Neue", serif;
  font-size: 20px;
  line-height: 55px;
  border: 2px solid #121212;
  border-radius: 200px;
}

.openPopup {
  cursor: pointer;
}

.dots .slick-dots {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.dots .slick-dots li {
  margin: 0 6px;
}

.dots .slick-dots li.slick-active button {
  opacity: 1;
}

.dots .slick-dots button {
  width: 10px;
  height: 10px;
  background-color: #fff;
  opacity: 0.3;
  border-radius: 50%;
  font-size: 0;
  transition: opacity 0.3s ease;
}

.header {
  position: relative;
}

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 30px 0;
  border-bottom: 2px solid #121212;
}

.header__nav {
  color: #fff;
}

.header--main .header__wrapper {
  border-color: transparent !important;
}

.header--black {
  background-color: #121212;
}

.header--black .header__wrapper {
  border-color: #fff;
}

.header--black .nav__link {
  color: #fff;
}

.header--black .nav__bar span {
  background-color: #fff;
}

.header--black .nav__bar span::before,
.header--black .nav__bar span::after {
  background-color: #fff;
}

.nav {
  display: flex;
  color: #121212;
}

.nav__main {
  display: flex;
  align-items: center;
}

.nav__body--open {
  display: block !important;
  max-width: 660px;
}

.nav__body--open::before {
  content: '';
  display: block;
  width: 200vw;
  height: 100vh;
  left: -50%;
  top: -26px;
  background: rgba(18, 18, 18, 0.4);
  position: absolute;
  z-index: -1;
}

.nav__contact {
  margin-top: 16px;
  display: none;
}

.nav__social .link--social {
  margin-right: 20px;
}

.nav__link {
  margin: 0 32px;
  font-weight: 500;
  color: inherit;
}

.nav__controls {
  display: flex;
}

.nav__control {
  margin: 0 20px;
}

.nav__bottom a {
  color: #121212;
}

.nav__phone {
  margin-top: 20px;
}

.nav__bar {
  display: none;
  width: 30px;
  height: 34px;
  cursor: pointer;
  position: relative;
  margin-left: auto;
}

.nav__bar span {
  display: block;
  position: absolute;
  top: 14px;
  left: 0;
  right: 0;
  height: 3px;
  background: #121212;
  transition: background 0.3s 0.3s;
  border-radius: 20px;
}

.nav__bar span::before,
.nav__bar span::after {
  content: '';
  display: block;
  width: 100%;
  height: 3px;
  background-color: #121212;
  border-radius: 20px;
  position: absolute;
  transition-duration: 0.3s, 0.3s;
  transition-delay: 0.3s, 0.3s;
}

.nav__bar span::before {
  transition-property: top, transform;
  top: -11px;
}

.nav__bar span::after {
  transition-property: bottom, transform;
  bottom: -11px;
}

.form {
  max-width: 780px;
}

.form__label {
  display: block;
}

.form__input {
  width: 100%;
  padding: 30px 6px;
  box-sizing: border-box;
  font-family: Inter, sans-serif;
  font-weight: 700;
  font-size: 15px;
  color: #121212;
  border: none;
  border-bottom: 2px solid #121212;
}

.form__input::placeholder {
  color: #A2A9B4;
  text-transform: uppercase;
}

.form__textarea {
  resize: none;
  height: 140px;
}

.form__bottom {
  display: flex;
  align-items: center;
  margin-top: 40px;
}

.form__caption {
  max-width: 270px;
  margin-left: 40px;
  color: #A2A9B4;
  font-size: 14px;
  line-height: 16px;
}

.form__caption a {
  text-decoration: underline;
}

.popup {
  display: none;
  opacity: 0;
  background: rgba(18, 18, 18, 0.4);
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 10000;
  transition: opacity 0.5s ease;
}

.popup--open {
  display: flex;
}

.popup__wrapper {
  justify-content: center;
  align-items: center;
  margin: auto 150px;
  width: 100%;
  max-height: 90vh;
  overflow-y: auto;
  background-color: #fff;
  cursor: default;
  position: relative;
}

.popup__close {
  position: absolute;
  top: 52px;
  right: 52px;
}

.popup__body {
  padding: 80px 135px 100px;
}

.popup__map {
  height: 100%;
  max-height: 440px;
  background: #F8F8FB url(../img/map.jpg) no-repeat center / cover;
}

.popup__map iframe {
  height: 440px;
}

.popup--contacts .popup__body {
  padding-top: 40px;
}

.popup--contacts .popup__title {
  margin: 40px 0;
}

.popup--reviews .popup__body,
.popup--opt .popup__body,
.popup--check .popup__body {
  display: flex;
  justify-content: space-between;
}

.popup--reviews .popup__info,
.popup--opt .popup__info,
.popup--check .popup__info {
  max-width: 376px;
  margin-right: 30px;
  flex-shrink: 0;
}

.popup--reviews .popup__text,
.popup--opt .popup__text,
.popup--check .popup__text {
  margin-top: 30px;
}

.popup--reviews .popup__content,
.popup--opt .popup__content,
.popup--check .popup__content {
  width: 100%;
  max-width: 780px;
}

.popup--reviews .popup__content p,
.popup--opt .popup__content p,
.popup--check .popup__content p {
  margin-bottom: 20px;
}

